.icon {
    position: relative;
    overflow: hidden;
    /* width: 50px;
    height: 50px; */
    
    border-radius:1px solid transparent;
    color: #fff;
    text-decoration: none;
    text-align: center;
    line-height: 70px;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
  
  .icon :hover{
        border-color: blueviolet;
        text-decoration: underline;
  }
  /* .icon:nth-child(1) { background: cornflowerblue; }
  .icon:nth-child(2) { background: salmon; }
  .icon:nth-child(3) { background: gray; } */
  
  /**
   * The "shine" element
   */
  
  .icon:after {
    
    -webkit-animation: shine 5s ease-in-out  infinite;
    
            animation: shine 5s ease-in-out  infinite;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;  
    content: "";
    position: absolute;
    top: -110%;
    left: -210%;
    width: 200%;
    height: 200%;
    opacity: 0;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
    
    background: rgba(255, 255, 255, 0.13);
    background: -webkit-gradient(
      linear, 
      left top, right top, 
      from(rgba(255, 255, 255, 0.13)),
      color-stop(77%, rgba(255, 255, 255, 0.13)),
      color-stop(92%, rgba(255, 255, 255, 0.5)),
      to(rgba(255, 255, 255, 0.0))
    );
    background: linear-gradient(
      to right, 
      rgba(255, 255, 255, 0.13) 0%,
      rgba(255, 255, 255, 0.13) 77%,
      rgba(255, 255, 255, 0.5) 92%,
      rgba(255, 255, 255, 0.0) 100%
    );
  }
  
  /* Hover state - trigger effect */
  
  
  /* Active state */
  
  .icon:active:after {
    opacity: 0;
  }
  
  @-webkit-keyframes shine{
    10% {
      opacity: 1;
      top: -30%;
      left: -0%;
      -webkit-transition-property: left, top, opacity;
      transition-property: left, top, opacity;
      -webkit-transition-duration: 0.7s, 0.7s, 0.15s;
              transition-duration: 0.7s, 0.7s, 0.15s;
      -webkit-transition-timing-function: ease;
              transition-timing-function: ease;
    }
    100% {
      opacity: 0;
      top: -30%;
      left: -60%;
      -webkit-transition-property: left, top, opacity;
      transition-property: left, top, opacity;
    }
  }
  
  @keyframes shine{
    10% {
      opacity: 1;
      top: -30%;
      left: -0%;
      -webkit-transition-property: left, top, opacity;
      transition-property: left, top, opacity;
      -webkit-transition-duration: 0.7s, 0.7s, 0.15s;
              transition-duration: 0.7s, 0.7s, 0.15s;
      -webkit-transition-timing-function: ease;
              transition-timing-function: ease;
    }
    100% {
      opacity: 0;
      top: -30%;
      left: -60%;
      -webkit-transition-property: left, top, opacity;
      transition-property: left, top, opacity;
    }
  }
